import React from 'react'
import DeviceItem from './DeviceItem'

class DeviceListView extends React.Component {

  constructor(props) {
    super(props)
    
    this.handleDelete = this.handleDelete.bind(this)
    this.handleEdit = this.handleEdit.bind(this)
  }

  handleDelete(e) {
    const id = Number(e.target.dataset.id)

    this.props.deleteDevice(id)
  }

  handleEdit(e) {
    const id = Number(e.target.dataset.id)
    const currentVal = this.props.devices.get(id)

    let text = window.prompt('', currentVal)

    this.props.editDevice(id, text)
  }

  render() {

    return (
      <div>
        {
          this.props.devices.map(function(device, index) {
            return (
              <div key={index}>
                {device}
                <button data-id={index} onClick={this.handleDelete}>X</button>
                <button data-id={index} onClick={this.handleEdit}>Edit</button>
              </div>
            )
          }.bind(this))
        }
      </div>
    )
  }
}

export default DeviceListView



