export function createDevice(text) {
  console.log('create!!!')
  return {
    type: 'CREATE_DEVICE',
    text
  }
}

export function editDevice(id, text) {
  return {
    type: 'EDIT_DEVICE',
    id,
    text
  }
}

export function deleteDevice(id) {
  return {
    type: 'DELETE_DEVICE',
    id
  }
}



